//*****************************************************
//*   PROJECT PAGE
//*****************************************************

@import "../../scss/variables";
@import "../../scss/mixins";

.project-bgImg-container {
  // border: 2px solid red;
  // @include bgImg(40vh, $main-bgImg-path);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .projects__bio-image {
    text-align: center;
    margin-bottom: 0.5rem;
  }
}

.projects {
  &__items {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 2rem 5rem;
    gap: 1rem 1rem;
  }
  &__item {
    width: 100%;
    cursor: pointer;
    border-bottom: 5px solid $secondary-color;
    position: relative;
    img {
      width: 100%;
    }

    &::after {
      content: "";
      width: 100%;
      height: 100%;
      background-color: $secondary-color;
      position: absolute;
      left: 0;
      opacity: 0;
      @include transition(0.5s);
    }
    &:hover::after {
      opacity: 0.7;
    }
  }
}

@include media-md {
  .projects__item {
    width: 100%;
  }
}

@include media-lg {


  .projects__item {
    width: 50%;
  }
  .contact__list {
    flex-direction: row;
  }
}

@include media-xl {
  .projects__item {
    width: 33%;
  }
}
