@import "../scss/variables";
@import "../scss/mixins";



.projects{
    &__items{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding: 2rem 5rem;
        gap: 1rem 0;
    }
    &__item{
        width: 100%;
        cursor: pointer;
        border-bottom: 5px solid $secondary-color;
        position: relative;
        img{
            width: 100%;
        }

        &::after{
            content: '';
            width: 100%;
            height: 100%;
            background-color: $secondary-color;
            position: absolute;
            left: 0;
            opacity: 0;
            @include transition(0.5s)
            
        }
        &:hover::after{
            opacity: 0.7;
        }
    }
}
